import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationSalesman from "../../Validations/validationSalesman";
import { renderField } from "../../renderField";

class FormSalesman extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kode_salesman: "",
      nama_salesman: "",
      rekening_rp: "",
      rekening_usd: "",
      plafond: "",
      kodeExt: "",
      status: "AKTIF",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-4 col-lg-4">
              <Field
                name="kodeSales"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Kode Salesman"
                limit={5}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="namaSales"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Nama Salesman"
              />
            </div>
            <div className="col-lg-12 col-md-12 mt-3">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Simpan
                  <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormSalesman = reduxForm({
  form: "Salesman",
  validate: validationSalesman,
  enableReinitialize: true,
})(FormSalesman);
export default connect()(FormSalesman);
