import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import FormSalesman from "./Component/FormSalesman";
import { editsalesmanList, getsalesmanList } from "../actions/salesmanAction";
import { connect } from "react-redux";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormSalesmanEdit from "./Component/FormSalesmanEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("sales/delete/" + kode)
        .then(() => dispatch(hideModal()))
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getsalesmanList())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}

const maptoState = (state) => {
  return {
    salemanList: state.provinsi.salesman,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class Salesman extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Salesman: [],
      response: "",
      isEdit: false,
      columns: [
        {
          dataField: "kode_sales",
          text: "Kode Salesman",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "nama_sales",
          text: "Nama Salesman",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_sales)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editsalesmanList(
                        row.kode_sales,
                        row.nama_sales,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsalesmanList());
  }
  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getsalesmanList());
  };

  handleSubmit(hasil) {
    const data = {
      kode_sales: hasil.kodeSales,
      nama_sales: hasil.namaSales,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("sales/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => LogData("Data Berhasil Ditambah Di Salesman", data))
          .then(() => this.props.dispatch(getsalesmanList()))
          .then(() => this.props.dispatch(reset("Salesman")))
      )
      .catch((err) =>
        this.handleReactive(err, hasil.kodeSales, {
          nama_sales: hasil.namaSales,
        })
      );
  }

  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(err, kode, "sales/reactive/", data, "sales/edit/").then(() =>
          this.props.dispatch(getsalesmanList())
        )
      : gagal("Data Gagal Ditambahkan");
  }
  handleEdit(hasil) {
    const data = {
      nama_sales: hasil.namaSales,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("sales/edit/" + hasil.kodeSales, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!")
          .then(() => LogData("Data Berhasil Diubah Di Salesman", data))
          .then(() => this.props.dispatch(getsalesmanList()))
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => LogData("Data Gagal Diubah Di Salesman", data, err))
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Salesman , Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Salesman  yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Salesman" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormSalesmanEdit
                        updateStatus={this.updateStatus}
                        onSubmit={(data) => this.handleEdit(data)}
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormSalesman
                        updateStatus={this.updateStatus}
                        onSubmit={(data) => this.handleSubmit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Salesman"
                      : "Tambah Data Salesman"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Salesman
                    </button>
                  </div>
                </div>
                <div className="col-lg-12" data-tut="reactour_2">
                  {this.props.salemanList ? (
                    <ToolkitProvider
                      keyField="kodeLokasi"
                      data={this.props.salemanList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group mt-5 mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <hr />
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            alt="ERROR GET DATA"
                            src={notrespond}
                            style={{ width: "30%" }}
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={300} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default connect(maptoState, null)(Salesman);
