const validationSalesman = (values) => {
    const errors = {}
    if (!values.kodeExt) {
        errors.kodeExt = 'Tidak Boleh Kosong'
      }
      if (!values.kodeSales) {
        errors.kodeSales = 'Tidak Boleh Kosong'
      } else if (values.kodeSales.length > 5) {
        errors.kodeSales = 'Kode Maksimal 5'
      }
      if (!values.namaSales){
          errors.namaSales = "Tidak Boleh Kosong"
      } else if (values.namaSales > 30){
          errors.namaSales = "Nama Terlalu Panjang"
      }
      return errors
}
 
export default validationSalesman;